$max: 100%;

@media (max-width: 1980px) {
	.close button {
		font-size: 25px;
	}

	.title__text {
		font-size: 25px;
	}

	.text {
		font-size: 18px;
	}
}

@media (max-width: 420px) {
	.close button {
		font-size: 25px;
	}

	.title__text {
		font-size: 23px;
	}

	.text {
		font-size: 15px;
	}
}

@media (max-width: 300px) {
	.close button {
		font-size: 15px;
	}

	.title__text {
		font-size: 20px;
	}

	.text {
		font-size: 12px;
	}
}

.modal {
	padding: 0 10px;
	position: fixed;
	top: 0;
	left: 0;
	height: $max;
	width: $max;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: .3s all ease-in-out;
	background-color: rgb(0, 0, 0, .3);
	opacity: 0;
	pointer-events: none;

	&__content {
		padding: 5px;
		width: 450px;
		height: 530px;
		border-radius: 10px;
		color: #49866d;
		background-color: #d9ffeb;
		transform: scale(0);
		transition: .3s all ease-in-out;

		b {
			font-weight: 400;
			text-decoration: underline;
		}

		.close {
			display: flex;
			justify-content: flex-end;
			align-items: center;

			& button {
				border: none;
				background: transparent;
				cursor: pointer;
				height: $max;
				width: 25px;
				transition: .1s all ease-in-out;
				border-radius: 5px;
				transition: .2s all ease-in-out;

				img {
					width: $max;
					height: $max;
				}

				&:hover {
					box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
				}
			}
		}

		.title {
			width: $max;
			height: 35px;
			display: flex;
			justify-content: center;
			align-items: center;

			&__text {
				font-weight: 400;
			}
		}

		.main__text {
			width: $max;
			height: 456px;

			&__content {
				height: $max;
				width: $max;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;
				padding: 20px;

				.block__bottom {
					width: $max;
					height: 40px;
					display: flex;
					justify-content: space-evenly;
					align-items: center;

					.modal__img {
						width: 10%;
					}

