$max: 100%;
$center: center;

@media (max-width: 1980px) {
	.header {
		max-width: 230px;
		height: 50px;

		& .text-header {
			font-size: 30px;
		}
	}
}

@media (max-width: 420px) {
	.header {
		max-width: 230px;
		height: 50px;

		& .text-header {
			font-size: 30px;
		}
	}
}

@media (max-width: 300px) {
	.header {
		max-width: 200px;
		height: 44px;

		& .text-header {
			font-size: 25px;
		}
	}
}

.header {
	width: $max;
	display: flex;
	justify-content: space-between;
	align-items: $center;
	margin: 50px auto;

	& .text-header {
		max-width: 450px;
		text-align: center;
		font-weight: 400;
		color: #49866d;
	}

	& .arrow {
		width: 15%;
		background-color: transparent;
	}

	& .settings {
		width: 22%;
		height: $max;
		cursor: pointer;
		background-color: #a4ffd2;
		border-radius: 50%;
		transition: .2s all ease-in-out;
		border: none;

		& img {
			width: $max;
			height: $max;
		}

		&:hover {
			transform: translateY(-2px)
		}

		&:focus {
			outline: 1px solid green;
		}
	}
}